import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Button, Container, Col, Form, Row } from 'react-bootstrap';

const Page = () => {
    // Â§âÊï∞ÂÆöÁæ©
    const [Title, setTitle] = useState(localStorage.getItem('takaratitle'))
    if (!Title) {
        setTitle("ÊäΩÈÅ∏‰ºö")
    }
    document.title = `Ë®≠ÂÆö`
    const navigate = useNavigate()
    const [TakaraList, setTakaraList] = useState(
        [
            { disabled: false, name: "[„Çµ„É≥„Éó„É´]ÊôØÂìÅ‚ë†" },
            { disabled: false, name: "[„Çµ„É≥„Éó„É´]ÊôØÂìÅ‚ë°" },
        ]
    )

    // ÂàùÂõûÂá¶ÁêÜ
    useEffect(() => {
        const data = JSON.parse(localStorage.getItem('takaralist'));
        if (data) {
            setTakaraList(data)
        }
    }, []);

    // Button Click Action
    const removeTakaralist = () => {
        const result = window.confirm('Êú¨ÂΩì„Å´„Çà„Çç„Åó„ÅÑ„Åß„Åô„ÅãÔºü');
        if (result === true) {
            localStorage.removeItem("takaralist");
            localStorage.removeItem("takaratitle");
            window.location.reload();
        }
    }
    const saveTakaralist = () => {
        localStorage.setItem('takaralist', JSON.stringify(TakaraList));
        localStorage.setItem('takaratitle', Title);
        const result = window.confirm('‰øùÂ≠òÊàêÂäü! MainPage„Å´ÈÅ∑Áßª„Åó„Åæ„Åô„ÅãÔºü');
        if (result === true) {
            navigate("/");
        }
    }
    const clickAddList = async () => {
        setTakaraList([...TakaraList, { disabled: false, name: null }]);
    }
    const handlechangeName = async (e, index) => {
        const copy = TakaraList.concat()
        copy[index].name = e
        setTakaraList(copy)
    }
    const handlechangeFlg = async (e, index) => {
        const copy = TakaraList.concat()
        copy[index].disabled = e
        setTakaraList(copy)
    }

    // „Éö„Éº„Ç∏„É¨„Ç§„Ç¢„Ç¶„Éà
    return (
        <div className="App">
            <header clasName="App-header">
                <Container fluid className="center">
                    <div className="card">
                    <h2 className="mt-2"><span>Ë®≠ÂÆö</span></h2>
                        <Form>
                            <Form.Group className="mb-3 + ml-2">
                                <Form.Control
                                    type="text"
                                    placeholder="„Çø„Ç§„Éà„É´"
                                    onChange={e => setTitle(e.target.value)}
                                    value={Title}
                                />
                            </Form.Group>
                            {TakaraList.map(function (value, index, array) {
                                return (
                                    <Row>
                                        <Col sm={1}>{index + 1}</Col>
                                        <Col sm={5}>
                                            <Form.Group className="mb-3 + ml-2">
                                                <Form.Control
                                                    type="text"
                                                    placeholder="ÊôØÂìÅÂêç"
                                                    onChange={e => handlechangeName(e.target.value, index)}
                                                    value={value.name}
                                                />
                                            </Form.Group>
                                        </Col>
                                        <Col sm={1}>
                                            <Form.Group className="mb-3" controlId="formBasicCheckbox">
                                                <Form.Check
                                                    type="checkbox"
                                                    label="ÁµÇ‰∫Ü"
                                                    onChange={e => handlechangeFlg(e.target.checked, index)}
                                                    checked={value.disabled}
                                                />
                                            </Form.Group>
                                        </Col>
                                    </Row>
                                )
                            })}
                        </Form>
                    </div>
                    < Button variant="primary" size="lg" onClick={() => clickAddList()}>++ ËøΩÂä† ++</Button>
                    < Button variant="primary" size="lg" onClick={() => saveTakaralist()}>‰øùÂ≠ò</Button>
                    < Button variant="primary" size="lg" onClick={() => removeTakaralist()}>ÂÖ®È†ÖÁõÆÂâäÈô§</Button>
                </Container>
            </header>
        </div >
    )
}

export default Page